body {
    background-color: rgba(31, 31, 31, 0.9);
    font-family: 'EB Garamond', serif;
    font-weight: 500;
    font-size: 1.2em;
    /* font-style: italic; */
}


.movie-list-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    color: whitesmoke;
    padding: 3rem;
}

.movie-card {
    width: 300px;
    height: 450px;
    perspective: 1000px;
    background-color: transparent;
    margin: 10px 0;
    border-radius: 50%;
}

.movie-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.7s;
    transform-style: preserve-3d;
    box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.2);
    border-radius: 5%;
}

.movie-card-front,
.movie-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    overflow: hidden;
    border-radius: 5%;
}

.movie-card-back {
    background-color: rgba(0, 0, 0, .85);
    transform: rotateY(180deg);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    overflow: auto;
    padding: 0 0.5rem;
}

.movie-card-back h4,
h5 {
    color: darkgoldenrod;
}

.movie-card:hover .movie-card-inner {
    transform: rotateY(180deg);
}

.movie-card:hover .movie-card .info {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    display: none;

}

.search-bar {
    width: 90%;
    margin: 3rem auto 1rem;
}

.search-bar input {
    width: 100%;
    padding: 0.8rem 0.5rem;
    border: 2px solid #ccc;
    border-radius: 10px;
    font-size: 1rem;
}
